import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-payment',
  templateUrl: './payment.component.html',
  styleUrls: ['./payment.component.scss']
})
export class PaymentComponent implements OnInit {

  show:boolean= true;

  action="Hide";

  constructor() { }

  hide=()=>{
    if(this.show===true ){

      this.show=false;
      this.action="Show"
    } else if (this.show===false){
      this.show=true;
      this.action="Hide"
    }
  }

  ngOnInit(): void {
  }

}
